<template>
  <div id="videoPort" v-show="!this.hiddenOcx" class="videoPort">
    <object
      style="background:#ccc;"
      :width="width"
      :height="heigth"
      id="realOcx"
      :codebase="codebase"
      classid="clsid:f3943464-829d-4bb8-8b05-10516831ceed"
    ></object>
  </div>
</template>

<script>
import ocxMethods from "./ocxMethods"; //ocx
export default {
  name:"Ocx",
  props: {
    hiddenOcx: Boolean,
    width: {
      type:String,
      default:'100%'
    },
     heigth:{
      type:String,
      default:'100%'
    },
     codebase:{
       type:String,
       default:'http://lib.cvtsp.com/video/OcxSetup2_0_2.CAB#version=2,0,2'
     },
     num:Number
  },
  mixins: [ocxMethods],
  data() {
    return {
      
    };
  },
  computed:{
      video(){
          return document.getElementById("realOcx")
      }
  },
  mounted() {
    this.setWh()
  },
  methods: {
   /**
     * 
     */
    nowPlay(obj){
      this.ocxInit(obj)
      this.ocxRealtimePlayXY(obj);
    },
    /**
     * 
     */
    hisPlay(obj){
       this.ocxInit( obj);
       this.ocxReplay(obj)
    },
    /**
     * 
     */
    setWh(){
      this.videoPortHeightC=window.innerHeight -
        document.getElementById("videoPort").getBoundingClientRect().top -
        80
        this.videoPortWidthC=document
        .getElementById("videoPort")
        .getBoundingClientRect().width
    },
     /**
     * 
     */
    setScreens(num) {
      setTimeout(() => {
        this.video.SetOrResetVideos(Number(num || 1));
      }, 500);
    },
    /**
     * OCX
     * @param {String} poster object id
     * @param {Object} options ip{ipAddress''port:'',userLevel:'',replayPower:'',picCutPower:'',ytCtrlPower:'',MTpowerPower:''}
     */
    ocxInit(options) {
      this.video.SetServerInfo(
        options.ipAddress,
        options.port,
        options.userLevel,
        options.replayPower,
        options.picCutPower,
        options.ytCtrlPower,
        options.MTpowerPower
      );
    this.setScreens(1)

    },

    /**
     * 
     * @param {String} poster :object id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxRealtimePlay( options) {
     this.video.StartRealTimeVideo(
        options.simCode,
        options.channel,
        options.StreamType,
        options.hasAudio
      );
    },

    /**
     * 
     * @param {String} poster :object id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxRealtimePlayXY(options) {
      this.video.StartRealTimeVideoProtocolStandard(
        options.simCode,
        options.channel,
        options.dataType,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} poster: object id
     * @param {Object} options  {simCode:simchannelMediaTypeStreamTypeStorageTypePlaybackModeMultiple/StartTimeEndTime}
     */
    ocxReplay( options) {
      this.video.PlayBack_Utc(
        options.simCode,
        options.channel,
        options.MediaType,
        options.StreamType,
        options.StorageType,
        options.PlaybackMode,
        options.Multiple,
        options.StartTime,
        options.EndTime,
        options.DataSource
      );

    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxCloseVideo(poster) {
     this.video.StopAll();
    },

    /**
     * 
     * @param {String} posterobject id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxSpeaking( options) {
     this.video.StartRTSpeaking(
        options.simCode.options.channel,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxStopSpeaking(poster) {
     this.video.StopRTSpeaking();
    },

    /**
     * 
     * @param {String} posterobject id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxMonitor( options) {
     this.video.StartMonitorVoice(
        options.simCode.options.channel,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxStopMonitor(poster) {
     this.video.StopMonitorVoice();
    }
  },watch: {
    num(val){
      this.setScreens(val)
    }
  },
};
</script>

<style lang="scss" scoped>
.videoPort{
  height:80%;
}
</style>