<template>
    <div id="player" class="playerfull" ></div>
</template>

<script>

import { asyncDownloadScript } from "../../../../utils/scriptHelper.js"
import service from "../../../../utils/service"
    export default {
         name:"H5Video",
        props: {
            //
             num:{
                default:4
            },
            // isFull:false,
            // 
           
            opt:Function
        },
        data(){
            return {
                isFull:false
            }
        },
        computed: {
            video_id(){
                return this.getSelectVideo().video_id
            }
        },
        created () {
          //js  
           asyncDownloadScript(
                "CvNetVideo",
                "http://lib.cvtsp.com/video/CVNetVideoJs/0.6.0/CvNetVideo.js"
            ).then(()=>{
                this.init()
            })
        },
        mounted () {
            //
             ["", "moz", "webkit", "ms"].forEach(val => {
                document.addEventListener(val + "fullscreenchange", () => {
                    this.isFull=this.isFullscreen()
                });
            });
        },
        beforeDestroy(){
            //
             this.Colse(-1);
        },
        methods: {
            /**
             *
             * 
             * @param {id}  id
             * @param {  num}  
             * @param { opt}   callback:
             */
            init(){
                if(this.opt){

                    CvNetVideo.Init(document.getElementById("player"), this.opt  );
                }else{

                    CvNetVideo.Init(document.getElementById("player"), this.num ||1 );
                }
            },
            //
            Resize(width, height){
                CvNetVideo.Resize(width, height);
            },
            //osd
            setOsdVisible(flag){
                CvNetVideo.SetOsdVisible(flag);
            },
            //osd
            setOsdColor(color){
                CvNetVideo.SetOsdColor(color);
            },
            //osd
            setOsdText(videoId,text){
                CvNetVideo.SetOsdText(videoId,text);
            },
            /**
             *
             * 
             * @param {sim, Channal:,MideaType:,StreamType:,StorageType:,PlaybackMode:,Multiple:,Starttime,Endtime,id,server:}
             */
            PlaybackVideo(obj){
                console.log(this.getSelectVideo())
                //
                if(!typeof obj =='object')return
                CvNetVideo.PlaybackVideo(
                    obj.simCode,//sim
                    obj.channel,//
                    obj.MideaType, //
                    obj.StreamType,//
                    obj.StorageType,//
                    obj.PlaybackMode,//
                    obj.Multiple,//
                    obj.Starttime, //
                    obj.Endtime,//
                    this.getSelectVideo().video_id,
                    {
                        clusterHost: obj.server,
                        clusterPort: obj.port
                    }
                    );
            },
            /**
             *
             * 
             * @param {sim,Channal:,StreamType:,true,id,server:}
             */
            StartRealTimeVideo(obj) {
               
                this.setOsdText(0,obj.plateCode+"/CH"+obj.channel)
                this.setOsdColor("#ffffff");
                CvNetVideo.StartRealTimeVideo(
                    obj.simCode,//sim
                    obj.channel,//
                    obj.StreamType,//
                    obj.flag,//
                   this.getSelectVideo().video_id,
                    {
                        clusterHost:obj.server,
                        clusterPort:obj.port
                    } 
                )
            },
            /**
             * 
             */
            playAll(obj){
               for (let index = 1; index <=obj.channel; index++) {
                    this.setOsdText(index,obj.plateCode+"/CH"+index);
                    this.setOsdColor("#ffffff");
                    CvNetVideo.StartRealTimeVideo(
                        obj.simCode,//sim
                        index, 
                        obj.StreamType,//
                        obj.flag,//
                        this.getSelectVideo().video_id, 
                        {
                            clusterHost:obj.server,
                            clusterPort:obj.port
                        } 
                    );
                }
            },
             /**
             * 
             */
            LayoutByScreens(num) {
                CvNetVideo.LayoutByScreens(num||this.num)
            },
             /**
             * 
             */
            fullscreen() {
                var elem = document.getElementById("player");
                service.requestFullScreen(elem)
            },
             /**
             * 
             */
            isFullscreen() {
                return (
                    document.fullscreen ||
                    document.webkitIsFullScreen ||
                    document.mozFullScreen ||
                    false
                );
            },
             /**
             * 
             */
            Resize(width, height) {
                CvNetVideo.Resize(width, height);
            },
            /**
             * osd
             */
            SetOsdColor(videoId, text) {
                CvNetVideo.SetOsdColor(videoId, text);
            },
            /**
             * osd
             */
            setOsdText(videoId, text) {
                CvNetVideo.SetOsdText(videoId, text);
            },
            /**
             * 
             */
           getSelectVideo() {
                return CvNetVideo.GetSelectedVideo();
            },
            /**
             *
             *  {: Colse(-1)}
             * @param {id}  id
             */
            Colse(id) {
                CvNetVideo.Stop(id);
            },
             /**
             *
             * 
             * @param {sim, ControlCommand:,Channal:, StreamType:, TurnOffMediaType:,id, server: }
             */
            AVTransferControl(obj) {
                CvNetVideo.AVTransferControl(
                    obj.simCode,//sim
                    obj.ControlCommand,//
                    obj.Channal,//
                    obj.StreamType,//
                    obj.TurnOffMediaType,//
                    this.getSelectVideo().video_id,
                    {
                     remoteHost: obj.server //server
                    }
                );
            },
             /**
             *
             * 
             * @param {sim, Channal:, PlaybackControl:, Multiple:, DragPlaybackPosition:, server:}
             */
            PlaybackVideoControl(obj) {
                CvNetVideo.PlaybackVideoControl(
                    obj.simCode,//sim
                    obj.Channal,//
                    obj.PlaybackControl,//
                    obj.Multiple,//
                    obj.DragPlaybackPosition,//
                    this.getSelectVideo().video_id,
                    {
                        remoteHost: obj.server
                    }
                );
            }
        },
        watch:{
            num(val){
                    this.LayoutByScreens(val)
            },
            isFull(val){
                setTimeout(() => {
                    this.LayoutByScreens(this.num)
                }, 100); 
            }
    }
       
    }
</script>

<style scoped>
#player{
    height:100%;
    width:100%;
    overflow-y: scroll;
}

.playerfull{
     /* height: 100%;width:100%; */
     /* overflow-y: scroll; */
}
.inContent-full {
            width: 100%;
            position: fixed;
            z-index: 9999;
            left: 0;
            top: 0;
            }
</style>