/**
 * ocxocx
 * @description ocxInit
 */

export default {
  methods: {
    /**
     * OCX
     * @param {String} poster object id
     * @param {Object} options ip{ipAddress''port:'',userLevel:'',replayPower:'',picCutPower:'',ytCtrlPower:'',MTpowerPower:''}
     */
    ocxInit(poster, options) {
      poster.SetServerInfo(
        options.ipAddress,
        options.port,
        options.userLevel,
        options.replayPower,
        options.picCutPower,
        options.ytCtrlPower,
        options.MTpowerPower
      );
    },

    /**
     * 
     * @param {String} poster :object id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxRealtimePlay(poster, options) {
      poster.StartRealTimeVideo(
        options.simCode,
        options.channel,
        options.StreamType,
        options.hasAudio
      );
    },

    /**
     * 
     * @param {String} poster :object id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxRealtimePlayXY(poster, options) {
      poster.StartRealTimeVideoProtocolStandard(
        options.simCode,
        options.channel,
        options.dataType,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} poster: object id
     * @param {Object} options  {simCode:simchannelMediaTypeStreamTypeStorageTypePlaybackModeMultiple/StartTimeEndTime}
     */
    ocxReplay(poster, options) {
      poster.PlayBack_Utc(
        options.simCode,
        options.channel,
        options.MediaType,
        options.StreamType,
        options.StorageType,
        options.PlaybackMode,
        options.Multiple,
        options.StartTime,
        options.EndTime,
        options.DataSource
      );
    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxCloseVideo(poster) {
      poster.StopAll();
    },

    /**
     * 
     * @param {String} posterobject id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxSpeaking(poster, options) {
      poster.StartRTSpeaking(
        options.simCode.options.channel,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxStopSpeaking(poster) {
      poster.StopRTSpeaking();
    },

    /**
     * 
     * @param {String} posterobject id
     * @param {Object} options :sim {simCode:'',channel:'',StreamType:''}
     */
    ocxMonitor(poster, options) {
      poster.StartMonitorVoice(
        options.simCode.options.channel,
        options.StreamType
      );
    },

    /**
     * 
     * @param {String} posterobject id
     */
    ocxStopMonitor(poster) {
      poster.StopMonitorVoice();
    }
  }
};
